const $siteList=$('.siteList')
const $lastLi=$siteList.find('li.last')
const data=localStorage.getItem('data')
const xObject=JSON.parse(data)  //把字符串变成对象
const hashMap= xObject||[
  {logo:'A',url:'https://www.acfun.cn'},
  {logo:'B',url:'https://www.bilibili.com'},
  {logo:'D',url:'https://www.douban.com/'},
  {logo:'G',url:'https://github.com/'},
  {logo:'T',url:'https://www.toutiao.com/'},
  {logo:'Z',url:'https://www.zhihu.com/'}
]

const simplifyUrl=(url)=>{
  return url.replace('https://','')
    .replace('http://','')
    .replace('www.','')
    .replace(/\/.*/,'')  //正则匹配 删除以/开头的内容
}

//render函数：遍历哈希表 渲染页面
const render=()=>{  
  $siteList.find('li:not(.last)').remove() 
  hashMap.forEach((node,index)=>{
    const $li=$(`
    <li>
      <div class="site">   
        <div class="logo">${node.logo}</div>  
        <div class="link">${simplifyUrl(node.url)}</div>  
        <div class="close">
          <svg class="icon">
            <use xlink:href="#icon-close"></use>
          </svg>
        </div>
      </div>
    </li>`).insertBefore($lastLi)
    $li.on('click',()=>{
      window.open(node.url)
    })
    $li.on('click','.close',(e)=>{  //阻止冒泡
      e.stopPropagation()
      hashMap.splice(index,1) 
      render()  //重新渲染
    })
  })
}

render() //渲染页面

$('.addButton')
.on('click',()=>{
  let url=window.prompt('请输入您想要添加的网址：')  //得到用户输入的那一串字符
  if(url.indexOf('http')!==0){
    url='https://'+url
  }
  hashMap.push({
    logo:simplifyUrl(url)[0].toUpperCase(),
    url:url
  })
  render()
})

window.onbeforeunload=()=>{
  const string=JSON.stringify(hashMap)  //把hashMap里的对象存成字符串
  localStorage.setItem('data',string)   //在本地的存储里设置一个x，其值为string  
}

//监听键盘事件
const keyboardLink = (e)=>{
  const{key} = e
  for(let i =0;i<hashMap.length;i++){
     if(hashMap[i].logo.toLowerCase() === key){
         window.open(hashMap[i].url,"_self")
     } 
  }}

$(document).on('keypress',keyboardLink)

$('.globalHeader input').on('keypress',(e)=>{
  e.stopPropagation();
}) 

